import config from '@/config/config';
export default {
	data() {
		return {
			userKey: '',
			order: '',
			mainItem: [],
			num: 0,
			price: 0,
			addressItem: {
				id: 0
			},
			remarks: '',
			isSubmit: false
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		var user = uni.getStorageSync('uerInfo');
		if (user != '') {
			this.userKey = user.userKey;
		}
		if (option.order) this.order = option.order;
		if (option.addressId) this.addressItem.id = option.addressId;

		if (user != '') {
			this.getProduct();
			this.getAddress();
		}
	},
	methods: {
		getProduct() {
			var _this = this;

			var goods_id = _this.order.split('_')[0];
			var num = _this.order.split('_')[1];

			_this.$request.product
				.getGoods({
					goods_id: goods_id
				})
				.then(data => {
					if (data.status == 1) {
						data.data.num = num;
						_this.mainItem = _this.mainItem.concat(data.data);
						_this.dealNum();
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		getAddress() {
			//获取收货地址
			var _this = this;
			//获取用户信息
			var _this = this;
			_this.$request.address
				.gradeList({})
				.then(data => {
					if (data.status == 1 && data.data.length > 0) {
						if (_this.addressItem.id > 0) {
							_this.addressItem = data.data.find(p => p.id == _this.addressItem.id);
						} else {
							_this.addressItem = data.data[0];
						}
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		dealNum() {
			//修改数量
			var nums = 0;
			var prices = 0;
			this.mainItem.forEach(its => {
				nums += its.num;
				prices += its.num * its.money;
			});
			this.num = parseInt(nums);
			this.price = prices;
		},
		dealOrder() {
			//处理订单
			var _this = this;
			if (_this.isSubmit) return false;
			if (_this.addressItem.id <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择收货地址'
				});
				return false;
			}
			_this.isSubmit = true;
			uni.showLoading({
				title: '数据提交中'
			});

			_this.$request.product
				.rush({
					goods_id: _this.mainItem[0].id,
					address_id: _this.addressItem.id
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						uni.showToast({
							icon: 'none',
							title: '购买成功'
						});
						setTimeout(() => {
							uni.navigateTo({
								url: '/pages/order/info?orderId=' + data.data.orderId
							});
						}, 1000)
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
					_this.isSubmit = false;
				})
				.catch(err => {
					_this.isSubmit = false;
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		selectAddress() {
			uni.navigateTo({
				url: '/pages/address/list?order=' + this.order + '&type=product'
			});
		}
	}
};
